body {
  min-width: 320px;
  margin: 0;
  font-family: 'Fira Sans', sans-serif;
  color: #191919;
  background-color: #fafafa;
  box-sizing: border-box;
  font-size: 10px;
}
h1 {
  font-size: 18px;
  font-family: 'PT Serif Caption', arial;
}
@media (min-width: 768px) {
  h1 {
    font-size: 3rem;
  }
}
h2 {
  font-size: 26px;
  font-family: 'PT Serif Caption', arial;
}
@media (min-width: 768px) {
  h2 {
    font-size: 30px;
  }
}
h3 {
  font-size: 14px;
  font-family: 'Fira Sans', sans-serif;
  text-transform: uppercase;
}
p {
  font-size: 16px;
}
a {
  text-decoration: none;
  text-align: center;
  color: #191919;
}
.highlighted {
  color: #ff7435;
}
.page-header {
  color: #191919;
  border: 1px solid red;
  display: flex;
  justify-content: center;
}
.page-header__inner {
  border: 2px solid green;
  display: flex;
  position: relative;
  margin: 20px;
  flex-wrap: wrap;
  align-items: center;
  flex-basis: 100%;
  justify-content: space-between;
}
.page-header__burger {
  background-image: url(/img/burger.png);
  background-repeat: no-repeat;
  background-size: cover;
  width: 30px;
  height: 30px;
  display: block;
  position: absolute;
  right: 0;
  top: 10px;
  z-index: 1;
}
@media (min-width: 1200px) {
  .page-header__burger {
    display: none;
  }
}
.page-header__contacts {
  display: none;
  font-size: 14px;
}
@media (min-width: 1200px) {
  .page-header__contacts {
    display: block;
    flex-basis: 25%;
  }
}
.page-header__phone {
  font-family: 'PT Sans Caption', sans-serif;
  font-size: 18px;
  font-weight: bold;
}
.page-header__btn {
  display: none;
}
@media (min-width: 1200px) {
  .page-header__btn {
    display: block;
  }
}
.logo {
  border: 1px solid #ebebeb;
  flex-grow: 1;
  flex-basis: 100%;
  display: flex;
  justify-content: center;
  width: 145px;
}
@media (min-width: 1200px) {
  .logo {
    flex-grow: 0;
    border: none;
    flex-basis: auto;
  }
}
.main-nav {
  font-size: 14px;
  text-transform: uppercase;
  flex-basis: 100%;
  display: none;
}
@media (min-width: 1200px) {
  .main-nav {
    display: block;
    flex-basis: 41.667%;
  }
}
.main-nav__list {
  list-style: none;
  padding: 0;
  margin: 0;
}
@media (min-width: 1200px) {
  .main-nav__list {
    padding: 0 10px;
  }
}
.main-nav__item {
  text-align: center;
  padding: 10px;
  border: 1px solid #ebebeb;
}
.main-nav__item:hover {
  color: #ff7435;
}
.main-nav__item--active {
  color: #ff7435;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.main-nav__item--active:after {
  content: "";
  width: 30px;
  height: 2px;
  background-color: #ff7435;
  display: block;
  margin-top: 3px ;
}
@media (min-width: 1200px) {
  .main-nav__item {
    border: none;
    display: inline-block;
  }
}
.btn {
  padding: 10px 20px;
  border-radius: 20px;
  color: #fff;
  text-transform: uppercase;
  font-size: 14px;
  font-family: 'Fira Sans', sans-serif;
}
.btn--orange {
  background-color: #ff7435;
}
.btn--orange:hover {
  background-color: #e85715;
}
.btn--picture:before {
  content: "";
  background-image: url(../img/calendar-btn-pic.png);
  background-repeat: no-repeat;
  background-size: contain;
  width: 15px;
  height: 15px;
  display: inline-block;
  vertical-align: middle;
  margin: 5px;
}
.btn--green {
  background-color: #0ab89d;
  border-radius: 0;
}
.btn--green:hover {
  background-color: #079b84;
}
.btn--transparent {
  background-color: transparent;
  border: 2px solid #fff;
}
.btn--transparent:hover {
  background-color: #fafafa;
  -webkit-text-fill-color: transparent;
}
.btn--more {
  color: #191919;
  text-decoration: underline;
  text-decoration-style: dotted;
  background-color: #fafafa;
}
.btn--more:hover {
  text-decoration: none;
}
.promo {
  border: 1px solid blue;
}
.promo-slider {
  border: 1px solid #000;
  position: relative;
}
.promo-slider__text {
  position: absolute;
  width: 100%;
  margin-top: 20px;
  top: 0;
  left: 0;
}
@media (min-width: 768px) {
  .promo-slider__text {
    margin: 20px;
    text-align: left;
    width: 75%;
  }
}
@media (min-width: 1200px) {
  .promo-slider__text {
    width: 50%;
  }
}
.promo-slider__title,
.promo-slider__info {
  background-color: rgba(250, 250, 250, 0.95);
  margin: 0;
  padding: 10px;
}
@media (min-width: 768px) {
  .promo-slider__title,
  .promo-slider__info {
    margin-bottom: 20px;
  }
}
.tabs {
  display: flex;
  justify-content: center;
}
.tabs__item {
  margin: 20px;
  font-size: 14px;
  text-transform: uppercase;
  color: #959595;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.tabs__item--active {
  color: #ff7435;
}
.tabs__item--active:after {
  content: "";
  width: 30px;
  height: 3px;
  background-color: #ff7435;
  display: block;
  margin-top: 5px ;
}
.health-block {
  display: flex;
  justify-content: center;
  border: 1px solid red;
  background-image: url(/img/health-promo-bgr.png);
  background-repeat: no-repeat;
  background-position: center top;
  background-size: cover;
}
.health-block__inner {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  flex-basis: 100%;
  text-align: center;
  border: 1px solid blue;
}
@media (min-width: 768px) {
  .health-block__inner {
    flex-basis: 75%;
  }
}
@media (min-width: 1200px) {
  .health-block__inner {
    flex-basis: 66.67%;
  }
}
.health-block__activities {
  display: none;
}
@media (min-width: 768px) {
  .health-block__activities {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    flex-basis: 100%;
  }
}
.health-block__item {
  flex-basis: 25%;
}
@media (min-width: 1200px) {
  .health-block__item {
    flex-basis: 16%;
  }
}
