@media (min-width: 1200px) {
  .gallery__inner {
    margin: 20px 100px 40px 100px;
  }
}
.gallery__img {
  width: 100%;
  height: auto;
  display: none;
}
@media (min-width: 768px) {
  .gallery__img {
    display: block;
  }
}
.testimonials {
  background-image: url(/img/testimonials-bgr.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  margin: 20px 0;
}
.testimonials__inner {
  text-align: center;
  width: 100%;
  padding: 50px 0;
}
.testimonials__title {
  color: #fff;
  margin-bottom: 50px;
}
.testimonials__slider {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.testimonials__btn {
  margin: 40px 0;
}
.review {
  background-color: #fafafa;
  flex-basis: 100%;
  text-align: center;
  display: none;
  padding-bottom: 10px;
  margin-bottom: 50px;
}
@media (min-width: 768px) {
  .review {
    flex-basis: 60%;
  }
  .review:nth-child(2) {
    display: block;
  }
}
@media (min-width: 1200px) {
  .review {
    flex-basis: 40%;
    margin-left: 20px;
    margin-right: 20px;
  }
}
.review__caption {
  font-weight: bold;
}
.review__img {
  margin-top: -20px;
}
.review__text {
  padding: 20px;
  margin: auto;
}
.review__dates {
  font-size: 14px;
}
.review:first-child {
  display: block;
}
.offers {
  text-align: center;
  position: relative;
}
@media (min-width: 1200px) {
  .offers__inner {
    margin: 0 100px;
  }
}
.offers__infoblock-title {
  color: #ff7435;
  text-align: left;
}
.offers__infoblock {
  text-align: left;
  padding: 10px;
  width: 90%;
  position: absolute;
  left: 0;
  bottom: 25px;
  background-color: rgba(250, 250, 250, 0.95);
}
@media (min-width: 768px) {
  .offers__infoblock {
    width: 35%;
  }
}
.offers__img {
  width: 100%;
}
.offers__slider .owl-dots {
  text-align: center;
}
.offers__slider .owl-dot {
  width: 15px;
  height: 15px;
  display: inline-block;
  border: 3px solid #ff7435;
  border-radius: 50%;
  margin: 15px;
}
.offers__slider .active {
  background-color: #ff7435;
}
.transport {
  text-align: center;
}
@media (min-width: 1200px) {
  .transport__inner {
    margin: 20px 100px 40px 100px;
  }
}
